.warp {
  width: 100%;
  position: relative;
  text-align: center; }
  .warp .warp-bg {
    width: 100%;
    display: flex;
    margin-bottom: 50rem;
    display: flex;
    flex-direction: row;
    justify-content: center; }
    .warp .warp-bg img {
      width: 100%;
      height: 50rem;
      position: absolute;
      top: 0.5rem;
      left: 0; }
    .warp .warp-bg img:nth-of-type(1) {
      animation: fadeInOut 15s linear 7s infinite; }
    .warp .warp-bg img:nth-of-type(2) {
      animation: fadeInOut 15s linear 5s infinite; }
    .warp .warp-bg img:nth-of-type(3) {
      animation: fadeInOut 15s linear 3s infinite; }
    .warp .warp-bg img:nth-of-type(4) {
      animation: fadeInOut 15s linear infinite; }

@keyframes fadeInOut {
  0% {
    opacity: 1; }
  17% {
    opacity: 1; }
  25% {
    opacity: 0; }
  92% {
    opacity: 0; }
  100% {
    opacity: 1; } }
  .warp .warp-text {
    width: 100%; }
    .warp .warp-text .text {
      width: 100%;
      display: flex; }
      .warp .warp-text .text .btn {
        background-color: #cde4f4 !important;
        border: 1px solid #adb4bc;
        border-radius: 1rem;
        margin-top: 1rem;
